import React, {useContext} from "react";
import './style.css'
import { AppContext } from "../../Context/context";


const Billete = ({valor, valorParaMostrar}) => {

    const {setCambio, totalPedido} = useContext(AppContext);

    const handleClick = () => {
        if(valor === 0) setCambio(0);
        else setCambio(valor - totalPedido);
    }
    return (
        <button
        onClick={handleClick}
        className="billete"
        >{valor ? `$ ${valorParaMostrar}` : `${valorParaMostrar}`}
        </button>
    )
};

export default Billete;