import {useState} from 'react';
import './App.css';
import ContenedorPrincipal from './Contenedor principal/ContenedorPrincipal';
import InicioSesion from './Inicio Sesion/InisioSesion';
import AppContextProvider from './Context/context';

function App() {
  
  const [inisioSesion, setInicioSesion] = useState(false);

  return (
    <AppContextProvider>
    <div className="App">
      {inisioSesion ? <ContenedorPrincipal inisioSesion={inisioSesion} setInicioSesion={setInicioSesion} />
      :
      <InicioSesion setInicioSesion={setInicioSesion}/>}
    </div>
    </AppContextProvider>
  );
};

export default App;
