import React, {useContext, useState, useRef} from 'react';
import './style.css';
import ResumenCompra from '../resumen compra/ResumenCompra';
import Billete from '../Billete/Billete';
import { AppContext } from '../../Context/context';

const Facturacion = () => {

    const {
      productoSeleccionado,
      setProductoSeleccionado,
      cantidad,
      setCantidad,
      productoParaAgregar,
      setProductoParaAgregar,
      pedido,
      setPedido,
      usuario,
      cambio,
      setCambio,
      setTotalPedido
    } = useContext(AppContext);

    const searchForm = useRef();
    
    const [textoBusqueda, setTextoBusqueda] = useState({search: ""});

    const handleSearchSubmit = async(event) => {
        event.preventDefault();
        const request = await fetch(`${process.env.REACT_APP_BACKEND_HOST}/api/producto/individual/${textoBusqueda.search}`);
        const data = await request.json();
        setProductoSeleccionado(data);
        setCantidad(1);
        searchForm.current.reset();
    };

    const handleSearchChange = (event) => {
        setTextoBusqueda({...textoBusqueda, [event.target.name]: event.target.value});
    };

    const handleMenosBotonClick = () => {
        let counter = cantidad  
        if(cantidad >= 2) setCantidad(counter -= 1);
    };

    const handleMasBotonClick = () => {
        let counter = cantidad  
        setCantidad(counter += 1);
    };

    const handleLimpiarBotonClick = () => {
        setProductoSeleccionado({});
        setCantidad(1);
        setCambio(0);
    };

    const handleAgregarBotonClick = () => {
        setPedido([...pedido, productoParaAgregar]);
        setCambio(0);
    };

    const handleCobrarClick = () => {
        setCambio(0);
        setCantidad(1);
        setPedido([]);
        setTotalPedido(0);
        setProductoParaAgregar({
            nombreProducto: "",
            cantidad: 1,
            unidad: "",
            precio: 0
        });
        setProductoSeleccionado({});
        searchForm.current.reset();
    };

    return(
        <div className="facturacion">

            <form onSubmit={handleSearchSubmit} ref={searchForm} className="facturaciÃ³n_form-buscar" >
                <input 
                onChange={handleSearchChange}
                className="facturaciÃ³n_form-buscar-input"
                type="text" 
                name="search"
                placeholder="CÃ³digo de producto"
                />
                <button className="facturaciÃ³n_form-buscar-button">Buscar</button>
            </form>

            {Object.values(productoSeleccionado).length !== 0 ?
            <div className="facturaciÃ³n_producto-seleccionado">
                <div className="facturacion_producto-seleccionado-header">
                    <h3 className="facturacion_producto-seleccionado-header-titulo">Producto Seleccionado</h3>
                </div>
                <h2 className="facturaciÃ³n_producto-seleccionado-titulo">{productoSeleccionado.nombre}</h2>
                <p className="facturaciÃ³n_producto-seleccionado-cantidad">{`${cantidad} ${productoSeleccionado.unidad}`}</p>
                <p className="facturaciÃ³n_producto-seleccionado-precio">{`$ ${productoParaAgregar.precio.toFixed(3)}`}</p>
                <div className="facturaciÃ³n_producto-seleccionado-contenedor-botones-cantidad">
                   
                    <button 
                    onClick={handleMenosBotonClick}
                    className="facturaciÃ³n_producto-seleccionado-botone-cantidad"
                    >Menos
                    </button>

                    <button
                    onClick={handleMasBotonClick}
                    className="facturaciÃ³n_producto-seleccionado-botone-cantidad"
                    >Mas
                    </button>

                </div>
                <div className="facturaciÃ³n_producto-seleccionado-contenedor-botones-agregar">
                    
                    <button
                    onClick={handleLimpiarBotonClick}
                    className="facturaciÃ³n_producto-seleccionado-botone-agregar"
                    >Limpiar
                    </button>

                    <button
                    onClick={handleAgregarBotonClick}
                    className="facturaciÃ³n_producto-seleccionado-botone-agregar"
                    >Agregar a la compra
                    </button>

                </div>
                
            </div>
            :
            <div className="facturaciÃ³n_producto-no-seleccionado">
                <h3 className="facturaciÃ³n_producto-no-seleccionado-titulo">Por favor busque o seleccione un producto</h3>
            </div>
            }
            

            <div className="facturaciÃ³n_contenedor-productos-mas-comprados">
                <div className="facturacion_producto-seleccionado-header">
                    <h3 className="facturacion_producto-seleccionado-header-titulo">Seleccione Billete Recibido</h3>
                </div>

                <div className="facturacion__contenedor-billetes">
                    <Billete valor={1.000} valorParaMostrar={"1.000"}/>
                    <Billete valor={2.000} valorParaMostrar={"2.000"}/>
                    <Billete valor={50.00} valorParaMostrar={"5.000"}/>
                    <Billete valor={10.000} valorParaMostrar={"10.000"}/>
                    <Billete valor={20.000} valorParaMostrar={"20.000"}/>
                    <Billete valor={50.000} valorParaMostrar={"50.000"}/>
                    <Billete valor={100.000} valorParaMostrar={"100.000"}/>
                    <Billete valor={0} valorParaMostrar={"Exacto"}/>
                </div>

                <button 
                onClick={handleCobrarClick}
                className="facturacion__boton-completar-venta"
                >Cobrar Pedido
                </button>

            </div>

            <p className="facturacion__cambio-title">{`Cambio: $${cambio === 0 ? 0 : cambio.toFixed(3)}`}</p>

            <h2 className="facturaciÃ³n_nombre-agente">{usuario.nombreReal}</h2>
            <ResumenCompra />
        </div>
    )
};

export default Facturacion;