import {createContext, useEffect, useState} from 'react'

export const AppContext = createContext();

const AppContextProvider = (props) => {

    //usuario que estÃ¡ logueado
    const [usuario, setUsuario] = useState({});

    //mensaje de error del inicio de sesion
    const [mensajeError, setTMensajeError] = useState("");

    //usuario y contraseÃ±a para inisciar sesion
    const [informacionUsuario, setInformacionUsuario] = useState({
        usuario: "",
        contrasena: ""
    });

    //prducto seleccionado en la interfaz de facturaciÃ³n
    const [productoSeleccionado, setProductoSeleccionado] = useState({});

    //cantidad de producto seleccionado en la interfaz de facturacion
    const [cantidad, setCantidad] = useState(1);

    //producto para agregar a el pedido
    const [productoParaAgregar, setProductoParaAgregar] = useState({
        nombreProducto: "",
        cantidad: 1,
        unidad: "",
        precio: 0
    });

    //Actualiza el producto para actualizar cada vez que se modifica la cantidad o ep productoSeleccionado
    useEffect(() => {
      setProductoParaAgregar({
        nombreProducto: productoSeleccionado.nombre,
        cantidad: cantidad,
        unidad: productoSeleccionado.unidad,
        precio: productoSeleccionado.precio * cantidad,
      });
    }, [cantidad, productoSeleccionado]);

    //pedido y todos sus productos y cantidades
    const [pedido, setPedido] = useState([]);

    //total del pedido
    const [totalPedido, setTotalPedido] = useState(0);

    //calcula el total del pedido cada que se agrega un nuevo producto en el estado llamado pedido
    useEffect(() => {
        const length = pedido.length;
        for(let i = 0; i < length && length > 0; i++){
            let total = totalPedido;
            setTotalPedido(total += pedido[i].precio);
        };
    }, [pedido]);

    //este es el cambio del cliente
    const [cambio, setCambio] = useState(0);

    //producto para crear en la base de datos
    const [productoParaCrear, setProductoPAraCrear] = useState({
        codigo: "",
        nombre: "",
        unidad: "",
        precio: ""
    });

    //todos los productos
    const [todosLosProductos, setTodosLosProductos] = useState([]);

    //Reiniciador de todos los productos cuando se agrega uno nuevo
    const [reiniciador, setReiniciador] = useState(1);

    //Carga nuevamente todos los productos cada vez que cambia el reiniciador
    useEffect(async () => {
        const request = await fetch(`${process.env.REACT_APP_BACKEND_HOST}/api/producto/todos`);
        const data = await request.json();
        setTodosLosProductos(data);
    }, [reiniciador]);

   

    return(
        <AppContext.Provider
        value={{
            mensajeError,
            setTMensajeError,
            informacionUsuario,
            setInformacionUsuario,
            productoSeleccionado,
            setProductoSeleccionado,
            cantidad,
            setCantidad,
            productoParaAgregar,
            setProductoParaAgregar,
            pedido,
            setPedido,
            totalPedido,
            setTotalPedido,
            usuario,
            setUsuario,
            cambio,
            setCambio,
            productoParaCrear,
            setProductoPAraCrear,
            reiniciador,
            setReiniciador,
            todosLosProductos
        }}>
            {props.children}
        </AppContext.Provider>
    )
};

export default AppContextProvider;