import React, {useContext} from 'react';
import './style.css';
import { AppContext } from '../../Context/context';

const Producto = ({codigo, nombre, unidad, precio}) => {

    const {reiniciador, setReiniciador} = useContext(AppContext);

    const handleClick = async() => {
      await fetch(`${process.env.REACT_APP_BACKEND_HOST}/api/producto/borrar/${codigo}`, {
        method: "DELETE"
      });
      let counter = reiniciador;
      setReiniciador((counter += 1));
    };

    return (
        <article className="producto">
            <div className="producto_contenedor-codigo">
                <p className="producto_codigo-titulo">CÃ³digo:</p>
                <h3 className="producto_codigo">{codigo}</h3>
            </div>

            <h3 className="producto_nombre">{nombre}</h3>

            <div className="producto__otra-informacion-contenedor">
                <p className="producto__otra-informacion">{unidad}</p>
                <p className="producto__otra-informacion">{`$ ${precio.toFixed(3)}`}</p>
            </div>

            <div className="producto__contenedor-botones">
                <button onClick={handleClick} className="producto__boton-eliminar">Eliminar</button>
            </div>
        </article>
    )
};

export default Producto;
