import React from 'react';
import "./style.css";
import {useHistory} from 'react-router-dom';


const Header = ({inisioSesion, setInicioSesion}) => {
    
    const history = useHistory();

    const handleFacturacion = () => {
      history.push("/");
    };

    const handleTodosLosProductos = () => {
      history.push("/productos");
    };

    const handleSalir = () => {
      if (inisioSesion) setInicioSesion(false);
    };

    return (
        <header className="header">

            <h1 className="header-titulo">Tienda de Abarrotes</h1>
            <div className="header_contenedor-botones">
                <button onClick={handleFacturacion} className="header_boton">FacturaciÃ³n</button>
                <button onClick={handleTodosLosProductos} className="header_boton">Todos los Productos</button>
                <button onClick={handleSalir} className="header_boton">Salir</button>
            </div>

        </header>
    )
};

export default Header;